import React, { Component, Fragment} from 'react'
import { Card, Table ,Tooltip,Button} from 'antd'
import ReactDOM from 'react-dom'
import { postAction } from "../../../../../axios"
import ReactHTMLTableToExcel from '../../../../../common/components/TableToExcel'

export default class BookList extends Component {

    state = {
        dataSource: [
            // { key: '1', sortIndex: '1', bookName: '大宋帝国', writer: '张三', publishing: '华中科技大学出版社', time: '2018-09-12'},
        ],
        allData:[],
        tableToExcel:{
            table: 'booklist-xls',
            filename: '在借图书列表excel下载'
        },
        pagination: {
            current: 1, // 当前页数，
            pageSize: 5, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
        totalSize:0
    }

    //在借图书列表
    getBookOnlineBorrow() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        let year = new Date().getFullYear();
        const { pagination: { current, pageSize } } = this.state;
        postAction(
            '/bigdata/book/bookOnlineBorrow',
            {
                studentNo: _studentNo,
                year: year,
                pageInfo: {
                    pageNum: current,
                    pageSize: pageSize
                }
            }
        ).then((res) => {
            if (res.success) {
                let objData = []
                res.obj.list && res.obj.list.map((element, index) => (
                    objData.push({
                        key: index + 1,
                        sortIndex: element.sortIndex,
                        bookName: element.bookName,
                        publishing: element.press,
                        time: element.day
                    })
                ))
                this.setState({
                    dataSource: objData,
                    pagination: {
                        current: res.obj.pageNum, // 当前页数，
                        pageSize: res.obj.pageSize, // 每页条数
                        total: res.obj.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    }
                })
            }
        })
    }

    getAllBookData() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        let year = new Date().getFullYear();
        postAction(
            '/bigdata/book/bookOnlineBorrow',
            {
                studentNo: _studentNo,
                year: year,
                pageInfo: {
                    pageNum: 1,
                    pageSize: this.state.pagination.total
                }
            }
        ).then((res) => {
            if (res.success) {
                let objData = []
                res.obj.list && res.obj.list.map((element, index) => (
                    objData.push({
                        key: index + 1,
                        sortIndex: element.sortIndex,
                        bookName: element.bookName,
                        publishing: element.press,
                        time: element.day
                    })
                ))
                this.setState({
                    allData: objData,
                })
            }
        })
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getBookOnlineBorrow();
            }
        )
    }

    componentDidMount() {
        this.getAllBookData();
        this.getBookOnlineBorrow();
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'booklist-xls')
        }
    }

    render() {

        const columns = [
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width: '10%',
            }, {
                title: '书籍名称',
                dataIndex: 'bookName',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }, {
                title: '出版社',
                dataIndex: 'publishing',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }, {
                title: '借书时间',
                dataIndex: 'time',
            }
        ]

        return (
            <Card
                title={<div className="common-title">本年度在借图书列表</div>}
                headStyle={{ border: 'none' }}
                style={{ height: 430 }}
                extra={
                    <Fragment>
                        <ReactHTMLTableToExcel
                            {...(this.state.tableToExcel || {})}
                            content={
                                <Tooltip placement="top" title='导出'>
                                    <Button theme="outlined" style={{padding:"0px 8px",marginLeft:"10px"}}>
                                        <i className="iconfont icon-daochu1" />
                                    </Button>
                                </Tooltip>
                            } 
                        />
                    </Fragment>
                }
            >
                <div>
                    <Table
                        height={this.props.height - 115}
                        columns={columns}
                        dataSource={this.state.dataSource}
                        size='middle'
                        bordered={true}
                        pagination={this.state.pagination}
                        onChange={this.handleTableChange.bind(this)}
                    />
                    <Table ref='table' columns={columns} dataSource={this.state.allData} pagination={false} style={{ display: 'none' }} />
                </div>
            </Card>
        )
    }
}


