import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table} from 'antd';
import LineChart from "../../../../../common/components/BizCharts/LineChart";
import { postAction } from "../../../../../axios"

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '月份',
  dataIndex: 'month',
},{
  title:'本人',
  dataIndex:'myself'
},{
  title:'平均',
  dataIndex:'everage'
}];
export default class Trend extends Component {

    state = {
        lineData: [
            // { month: "1月", "本人": 7.0, "平均": 3.9 },
        ],
        tab: '1',
        chartIns: undefined,
        total: 0,
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getBookBorrow() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        let year = new Date().getFullYear()
        postAction(
            '/bigdata/book/bookBorrowTendency',
            {
                studentNo: _studentNo,
                year: year
            }
        ).then((res) => {
            if (res.success && res.obj !== '') {
                let tendency = [],newTable = []
                res.obj.forEach(element => {
                    tendency.push({
                        month: element.borrowMonth,
                        本人: element.borrowTimesCount,
                        平均: element.borrowTimesAvg
                    })
                })
                res.obj.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        month: element.borrowMonth, 
                        myself: element.borrowTimesCount+'本',
                        everage: element.borrowTimesAvg+'本',
                    })
                });
                this.setState({
                    lineData: tendency,
                    tableData: newTable,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'jieyue-xls')
        }
      })
    }

    componentDidMount() {
        this.getBookBorrow()
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">本年度借阅趋势<span>单位：本</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "本年度借阅趋势"
                }}
                tableToExcel={{
                  table: 'jieyue-xls',
                  filename: '本年度借阅趋势excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    <LineChart
                        data={this.state.lineData}
                        padding={[10, 40, 60, 60]}
                        height={this.props.height - 100}
                        colors={["#1890ff", "#2FC25B"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:290}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


