import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table } from 'antd';
import { postAction } from "../../../../../../axios";
import BarChart from './BarChart';
// import NoData from '@/common/components/NoData/NoDataComponent';

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '日期',
  dataIndex: 'month',
  render: (text) => {
      return (
          <span title={text}>{text}</span>
      )
  }
}, {
  title: '早餐(元)',
  dataIndex: 'breakfast'
},{
  title:'中餐(元)',
  dataIndex:'Chinesefood'
},{
  title:'晚餐(元)',
  dataIndex:'dinner'
},{
    title:'三餐总金额(元)',
    dataIndex:'total'
}];
export default class ThreeMeals extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            total: 0,
            barData: [
                // {
                // label: "12-1",
                // "早餐": 2,
                // "中餐": 6,
                // "晚餐": 12
                // }
            ],
            tableData:[],
            pagination: {
                current: 1, // 当前页数，
                pageSize:5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                  return (
                    `共${total}条数据`
                  )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        postAction(
            "/bigdata/consume/getLatestDietSituationData",
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if(res.success){
                let newData = [],newTable = [];
                res.obj.forEach(
                    (item) => {
                        newData.push({
                            label: item.consumeDate,
                            "早餐": Number(item.breakfastAmount),
                            "中餐": Number(item.lunchAmount),
                            "晚餐": Number(item.supperAmount),
                            "三餐总金额": Number(item.totalAmount)
                        })
                    }
                )
                res.obj.forEach((item,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        month: item.consumeDate, 
                        breakfast: Number(item.breakfastAmount),
                        Chinesefood: Number(item.lunchAmount),
                        dinner: Number(item.supperAmount),
                        total: Number(item.totalAmount).toFixed(2)
                    })
                })

                this.setState({
                    barData: newData,
                    tableData:newTable,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            } else {
                this.setState({
                    barData: [],
                    newTable:[],
                    pagination: {
                        current: 1, // 当前页数，
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'threeMeals-xls')
        }
      })
    }

    render() {

        // if (this.state.barData && this.state.barData[0] && this.state.total > 0){
            return(
                <MainCard
                    title={<div className="common-title">近七天三餐就餐情况<span>单位：元</span></div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    // detailCallback={this.mingxi}
                    downloadChart={{
                        chartIns: this.state.chartIns,
                        title: "近七天三餐就餐情况"
                    }}
                    tableToExcel={{
                        table: 'threeMeals-xls',
                        filename: '近七天三餐就餐情况excel下载'
                    }}
                >
                    {
                        this.state.tab === '1'
                            ?
                            <BarChart
                                data={this.state.barData}
                                geomType={'intervalStack'}
                                height={this.props.height - 120}
                                padding={[10,40,60,60]}
                                colors={["#1890FF","#2FC25B","#F5A623","#F04864"]}
                                onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            />
                            :
                            <div>
                                <Table
                                    dataSource={this.state.tableData}
                                    columns={columns}
                                    bordered
                                    size="small"
                                    pagination={this.state.pagination}
                                    style={{height:286}}
                                />
                                <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                            </div>
                    }
                </MainCard>
            )
        // } else {
        //     return (
        //         <Card
        //             title={<div className="common-title">近七天三餐就餐情况<span>单位：元</span></div>}
        //             headStyle={{ border: "none" }}
        //             bodyStyle={{ padding: 0 }}
        //
        //         >
        //             <NoData height={334} data={'nodata'} />
        //         </Card>
        //     )
        // }

    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)){
            setTimeout(() => {
                this.getData()
            })
        }
    }

}


