import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom'
import { Card, Table,Button,Tooltip } from 'antd';
import { postAction } from "../../../../../axios"
import ReactHTMLTableToExcel from '../../../../../common/components/TableToExcel';

export default class Record extends Component {

    state = {
        dataSource: [],
        allData:[],
        tableToExcel:{
            table: 'internet-xls',
            filename: '本年度上网记录excel下载'
        },
        pagination: {
            current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
        totalSize:0
    }

    getByOneself() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        let year = new Date().getFullYear();
        const { pagination: { current, pageSize } } = this.state;
        postAction(
            '/bigdata/netTime/surfRecordByOneself',
            {
                year: year,
                studentNo: _studentNo,
                pageInfo: {
                    "pageNum": current,
                    "pageSize": pageSize
                }
            }
        ).then((res) => {
            if (res.success) {
                let newData = []
                res.obj.list && res.obj.list.map(({ address, loginTime, stayTime }, index) => (
                    newData.push({
                        key: index + 1,
                        sortIndex: index + 1,
                        time: loginTime,
                        onLine: stayTime,
                        address: address
                    })
                ))
                this.setState({
                    dataSource: newData,
                    pagination: {
                        current: res.obj.pageNum, // 当前页数，
                        pageSize: res.obj.pageSize, // 每页条数
                        total: res.obj.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    }
                })
            }
        })
    }

    getAllData () {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        let year = new Date().getFullYear();
        postAction(
            '/bigdata/netTime/surfRecordByOneself',
            {
                year: year,
                studentNo: _studentNo,
                pageInfo: {
                    "pageNum": 1,
                    "pageSize": this.state.pagination.total
                }
            }
        ).then((res) => {
            if (res.success) {
                let newAllData = []
                res.obj.list && res.obj.list.map(({ address, loginTime, stayTime }, index) => (
                    newAllData.push({
                        key: index + 1,
                        sortIndex: index + 1,
                        time: loginTime,
                        onLine: stayTime,
                        address: address
                    })
                ))
                this.setState({
                    allData: newAllData,
                })
            }
        })
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getByOneself()
            }
        )
    }

    componentDidMount() {
        this.getByOneself();
        this.getAllData();
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'internet-xls')
        }
    }

    render() {

        const columns = [{
            title: '序号',
            dataIndex: 'sortIndex',
            width: '10%',
            align: 'center'
        }, {
            title: '上网时间',
            dataIndex: 'time',
            align: 'center'
        }, {
            title: '在线时长',
            dataIndex: 'onLine',
            align: 'center'
        }, {
            title: '地点',
            dataIndex: 'address',
            align: 'center'
        }];

        return (
            <Card
                title={<div className="common-title">本年度上网记录</div>}
                headStyle={{ border: 'none' }}
                bodyStyle={{ paddingTop: 0 }}
                extra={
                    <Fragment>
                        <ReactHTMLTableToExcel
                        {...(this.state.tableToExcel || {})}
                            content={
                                <Tooltip placement="top" title='导出'>
                                    <Button theme="outlined" style={{padding:"0px 8px",marginLeft:"10px"}}>
                                        <i className="iconfont icon-daochu1" />
                                    </Button>
                                </Tooltip>
                            } 
                        />
                    </Fragment>
                }
            >
                <Table
                    dataSource={this.state.dataSource}
                    columns={columns}
                    size='middle'
                    pagination={this.state.pagination}
                    bordered={true}
                    onChange={this.handleTableChange.bind(this)}
                />
                <Table ref='table' columns={columns} dataSource={this.state.allData}  pagination={false} style={{display:'none'}} />
            </Card>
        )
    }
}


