import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table} from 'antd';
import PieChart from "../../../../../common/components/BizCharts/PieChart";
import { postAction } from "../../../../../axios"

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '地点',
  dataIndex: 'address',
},{
  title:'数量',
  dataIndex:'num'
}]
export default class Classification extends Component {

    state = {
        pieData: [
            // { item: '计算机', count: 40 },
        ],
        tab: '1',
        chartIns: undefined,
        total: 0,
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getBookBorrowType() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        let year = new Date().getFullYear()
        postAction(
            '/bigdata/book/bookBorrowTypeAnalyze',
            {
                studentNo: _studentNo,
                year: year
            }
        ).then((res) => {
            if (res.success) {
                let bookTop = [],newTable = []
                res.obj.forEach(element => {
                    bookTop.push({
                        item: element.bookType,
                        count: element.typeBorrowTimes
                    })
                })
                res.obj.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        address:element.bookType, 
                        num: element.typeBorrowTimes+'本',
                    })
                })
                this.setState({
                    pieData: bookTop,
                    tableData: newTable,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }
    
    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'book-xls')
        }
      })
    }

    componentDidMount() {
        this.getBookBorrowType()
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">本年度借阅图书分类TOP5<span>单位：本</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "本年度借阅图书分类TOP5"
                }}
                tableToExcel={{
                  table: 'book-xls',
                  filename: '本年度借阅图书分类TOP5excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    <PieChart
                        height={301}
                        data={this.state.pieData}
                        padding={[10, 10, 95, 10]}
                        innerRadius={0.55}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                    />
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:301}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


