import React, { Component } from 'react'
import { Tabs } from 'antd'

import Xueye from "./xueye/index"
import Xiaofei from "./xiaofei/index"
import Librtry from "./librtry/index"
import Internet from "./internet/index"
import Huaxiang from "./huaxiang"
import { postAction } from "../../../../axios"
import {getLocal } from "../../../../utils"
import '../../all/style.less'

const TabPane = Tabs.TabPane
export default class Details extends Component {
    state={
        tabs:[]
    }
    callback(key) {
        // console.log(key)
    }
    componentDidMount() {
        this.gettabsfromback()
    }
    gettabsfromback(){
        const userinfo=JSON.parse(getLocal('userInfo'))
        let uid=userinfo.id
        let data={
            sourceCode:"pc",
            menuId:12,
            userId:uid
            
        }
        postAction('/bigdata/system/menu/labelList',data,0,1).then(res=>{
           if(res.success){
               this.setState({
                  tabs:res.obj
               })
           }
        })
    }
    getTabs=id=>{
        switch(id){            
            case 122:
            return <Huaxiang/>
            case 123:
            return <Xueye/>
            case 124:
            return <Xiaofei/>
            case 125:
            return  <Librtry/>
            case 126:
            return <Internet/>           
            default:
            return null
        }
    }
    render() {
        const {tabs}=this.state
        return (
            <Tabs defaulActiveKey="122" onChange={this.callback} className="main-tabs main-content">
                {
                    tabs.map(item=>(
                        item.id !== 121?
                        <TabPane tab={item.name} key={item.id}>
                            {this.getTabs(item.id)}
                        </TabPane>:null
                    ))
                }   
               
            </Tabs>
        )
    }
}


