import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table } from 'antd';
import { postAction } from "../../../../../axios"
import PieChart from "../../../../../common/components/BizCharts/PieChart";

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '地点',
  dataIndex: 'address',
},{
  title:'次数',
  dataIndex:'num'
}];
export default class Place extends Component {

    state = {
        tab: '1',
        chartIns: undefined,
        total: 0,
        pieData: [],
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getPerConsumePlace() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        postAction(
            '/bigdata/consume/getPerConsumePlace',
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if (res.success) {
                let dataObj = [],newTable = []
                res.obj.forEach(element => {
                    dataObj.push({
                        item: element.name,
                        count: element.value
                    })
                });
                res.obj.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        address: element.name, 
                        num: element.value+'次'
                    })
                });
                this.setState({
                    pieData: dataObj,
                    tableData:newTable,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'Top5-xls')
        }
      })
    }

    componentDidMount() {
        this.getPerConsumePlace()
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">本学年刷卡地点分布TOP5<span>单位：次</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                // detailCallback={this.mingxi}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "本年度刷卡地点分布TOP5"
                }}
                tableToExcel={{
                  table: 'Top5-xls',
                  filename: '本年度刷卡地点分布TOP5excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    <PieChart
                        data={this.state.pieData}
                        padding={[0, 0, 60, 0]}
                        height={this.props.height - 100}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:290}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


