import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom'
import { Card, Button, Tooltip, Table, Select, message } from 'antd';
import { postAction } from "../../../../../axios"
import ReactHTMLTableToExcel from '../../../../../common/components/TableToExcel';
import {hostUrl} from "../../../../../env";


const Option = Select.Option;
const columns1 = [{
    title: '序号',
    dataIndex: 'sortIndex',
    width: '10%',
    align: 'center'
}, {
    title: '课程名称',
    dataIndex: 'name',
    align: 'center'
}, {
    title: '课程类别',
    dataIndex: 'type',
    align: 'center'
}, {
    title: '成绩',
    dataIndex: 'results',
    align: 'center'
}, {
    title: '班级排名',
    dataIndex: 'classRanking',
    align: 'center'
}, {
    title: '专业排名',
    dataIndex: 'professionalRanking',
    align: 'center'
}];

export default class Paiming extends Component {

    state = {
        columns: columns1,
        dataSource: [],
        semesterLists: [], //个人成绩排名学期筛选条件
        beginDate: '',
        endDate: '',
        tableToExcel: {
            table: 'class-xls',
            filename: '个人成绩排名excel下载'
        },
        pagination: {
            // current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
        ranking:"0/0"
    }

    // (公共课排名)

    getScorePublic = () => {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        const begin = this.state.beginDate.split("-");
        const lasbegin = begin[1].split("");
        const _beginDate = `${begin[0]}${lasbegin[lasbegin.length-1]}`;

        const end = this.state.endDate.split("-");
        const lasEnd = end[1].split("");
        const _endDate = `${end[0]}${lasEnd[lasEnd.length-1]}`;
        postAction(
            '/bigdata/score/personalScoreRank',
            {
                studentNo: _studentNo,
                beginDate: _beginDate,
                endDate: _endDate,
                // pageInfo: {
                //     pageNum: current,
                //     pageSize: pageSize
                // }
            }
        ).then((res) => {
            if (res.success) {
                if (res.obj && res.obj && res.obj.length > 0) {
                    let newData = []
                    res.obj.map((item, index) => (
                        newData.push({
                            key: index + 1,
                            sortIndex: index + 1,
                            name: item.courseName,
                            type: item.courseType,
                            results: item.score,
                            classRanking: item.classRownum,
                            professionalRanking: item.majRownum
                        })
                    ))
                    this.setState({
                        dataSource: newData,
                        pagination: {
                            // current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: res.obj.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                } else {
                    this.setState({
                        dataSource: [],
                        pagination: {
                            // current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        })
    }

    // 学年学期筛选条件
    getQueryStudentSemester(){
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        postAction(
            "/bigdata/studentScoresWeight/queryStudentSemester",
            {
                studentNo: _studentNo
            }
        ).then(
            (res) => {
                if (res.success) {
                    if (res.obj && res.obj.length > 0) {
                        this.setState({
                            semesterLists: res.obj,
                            beginDate: res.obj[2].semesterYear + '' + res.obj[2].semester,
                            endDate: res.obj[0].semesterYear + '' + res.obj[0].semester,
                        }, () => {
                            this.getScorePublic();
                            this.getRankingData();
                        })
                    }
                }
            }
        )
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getScorePublic()
            }
        )
    }

    //更换学期
    changeBeginDate = (e) => {
        let beginDateYear = e.substring(0, 9);
        let beginDateSemester = e.substring(9, 10);
        let endDateYear = this.state.endDate.substring(0, 9);
        let endDateSemester = this.state.endDate.substring(9, 10);
        if(beginDateYear > endDateYear){
            message.warn('起始时间不能晚于截止时间');
            return;
        }else if(beginDateYear === endDateYear) {
            if(beginDateSemester > endDateSemester) {
                message.warn('起始时间不能晚于截止时间');
                return;
            }
        }
        this.setState(
            {
                beginDate: e
            }
        )
    }

    //更换学期
    changeEndDate = (e) => {
        let beginDateYear = this.state.beginDate.substring(0, 9);
        let beginDateSemester = this.state.beginDate.substring(9, 10);
        let endDateYear = e.substring(0, 9);
        let endDateSemester = e.substring(9, 10);
        if(beginDateYear > endDateYear){
            message.warn('截止时间不能早于起始时间');
            return;
        }else if(beginDateYear === endDateYear) {
            if(beginDateSemester > endDateSemester) {
                message.warn('截止时间不能早于起始时间');
                return;
            }
        }
        this.setState(
            {
                endDate: e
            }
        )
    }

    //个人学业情况（专业）排名接口获取
    getRankingData() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        let _majorCode = JSON.parse(localStorage.getItem("studentItem")).majorCode;

        const begin = this.state.beginDate.split("-");
        const lasbegin = begin[1].split("");
        const _beginDate = `${begin[0]}${lasbegin[lasbegin.length-1]}`;

        const end = this.state.endDate.split("-");
        const lasEnd = end[1].split("");
        const _endDate = `${end[0]}${lasEnd[lasEnd.length-1]}`;

        let data = {
            studentNo: _studentNo,
            majorCode: _majorCode,
            beginDate: _beginDate,
            endDate: _endDate,
        }

        postAction(
            "/bigdata/studentScoresWeight/queryPersonalRank",data
        ).then(
            (res) => {
                if (res.success){
                    this.setState({
                        ranking: res.obj[0].rank
                    })
                }
            }
        )
    }

    //查询
    search = () => {
        this.getScorePublic();
        this.getRankingData();
    }

    componentDidMount() {
        this.getQueryStudentSemester();
        //默认下载专业课指定ref
        if (this.refs['table']) {
            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
            const table = tableCon.querySelector('table')
            table.setAttribute('id', 'class-xls')
        }
    }

    changeTab = (e) => {
        if (this.refs['table']) {
            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
            const table = tableCon.querySelector('table')
            table.setAttribute('id', 'class-xls')
        }
    }

    handleMingxi = () => {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        const begin = this.state.beginDate.split("-");
        const lasbegin = begin[1].split("");
        const _beginDate = `${begin[0]}${lasbegin[lasbegin.length-1]}`;

        const end = this.state.endDate.split("-");
        const lasEnd = end[1].split("");
        const _endDate = `${end[0]}${lasEnd[lasEnd.length-1]}`;

        window.location.href=`${hostUrl}/bigdata/studentScoresWeight/exportStudentScore?studentNo=${_studentNo}&beginDate=${_beginDate}&endDate=${_endDate}&reorder=3`;
    }

    render() {
        const { columns } = this.state
        return (
            <Card
                title={<div className="common-title">个人成绩排名</div>}
                headStyle={{ border: 'none' }}
                bodyStyle={{ paddingTop: 0 }}
                onChange={this.changeTab}
                extra={
                    <Fragment>
                        <span>个人学业情况（专业）：{this.state.ranking}</span>
                        <ReactHTMLTableToExcel
                        {...(this.state.tableToExcel || {})}
                            content={
                                <Tooltip placement="top" title='导出'>
                                    <Button theme="outlined" style={{padding:"0px 8px",marginLeft:"10px"}}>
                                        <i className="iconfont icon-daochu1" />
                                    </Button>
                                </Tooltip>

                            } 
                        />
                        <Tooltip placement="top" title='导出明细'>
                            <Button onClick={this.handleMingxi} style={{marginLeft:10,padding:"0px 8px"}} theme="outlined">
                                <i className="iconfont icon-daochu" />
                            </Button>
                        </Tooltip>
                    </Fragment>
                }
            >
                <div style={{float:'right',marginBottom:'20px'}}>

                    <Select 
                		value={this.state.beginDate}
                		placeholder="请选择学期" 
                		style={{ width: 220 }}
                		onChange={this.changeBeginDate}
                	>
                	    {
                	        this.state.semesterLists.length > 0 ? this.state.semesterLists.map((item, index) => <Option key={index} value={item.semesterYear + '' + item.semester}>{item.semesterYear + '学年第' + item.semester + '学期'}</Option>):null
                	    }
                	</Select>
                    <span style={{marginRight:10}}>至</span>
                    <Select 
                		value={this.state.endDate}
                		placeholder="请选择学期" 
                		style={{ width: 220 }}
                		onChange={this.changeEndDate}
                	>
                	    {
                	        this.state.semesterLists.length > 0 ? this.state.semesterLists.map((item, index) => <Option key={index} value={item.semesterYear + '' + item.semester}>{item.semesterYear + '学年第' + item.semester + '学期'}</Option>):null
                	    }
                	</Select>
                    <Button type='primary' onClick={this.search}>查 询</Button>
                </div>
                <Table
                    className="orCard"
                    dataSource={this.state.dataSource}
                    columns={columns}
                    bordered={true}
                    size="middle"
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange.bind(this)}
                />
                <Table ref='table' columns={columns} dataSource={this.state.dataSource} pagination={false} style={{ display: 'none' }} />
            </Card>
        )
    }
}


