import React, { Component, Fragment } from 'react';
import { Row, Col } from 'antd';
import Health from "./Health";
import TimeLong from "./TimeLong";
import Record from "./Record";


export default class Internet extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col span={12} className="card-padding card-right">
                        <Health height={400} />
                    </Col>

                    <Col span={12} className="card-padding card-left">
                        <TimeLong height={400} />
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <Record />
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


