import React, { Component } from 'react';
import { Table} from 'antd';
import ReactDOM from 'react-dom'
import { BarLineChart } from '../../../../../common/components/BizCharts/Biaxial';
import { postAction } from "../../../../../axios"
import MainCard from "@/common/components/MainCard"
import {hostUrl} from "../../../../../env";


const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '学期',
  dataIndex: 'semester',
  render: (text) => {
      return (
          <span title={text}>{text}</span>
      )
  }
}, {
  title: '专业平均（柱状）',
  dataIndex: 'majAvg'
},{
  title:'个人（柱状）',
  dataIndex:'oneAvg'
}, {
    title: '专业平均（折线）',
    dataIndex: 'majGpa'
},{
    title:'个人（折线）',
    dataIndex:'oneGpa'
}];

export default class History extends Component {

    state = {
        tab: '1',
        chartIns: undefined,
		total: 0,
        lineData: [  
            // { name: "2019", '专业平均（柱状）': 6.9, '个人（柱状）': 3.9, '专业平均（折线）': 80, '个人（折线）': 80 },
            // { name: "2018", '专业平均（柱状）': 9.5, '个人（柱状）': 4.6, '专业平均（折线）': 100, '个人（折线）': 80 },
            // { name: "2017", '专业平均（柱状）': 14.2, '个人（柱状）': 6.9, '专业平均（折线）': 100, '个人（折线）': 100 },
            // { name: "2016", '专业平均（柱状）': 18.5, '个人（柱状）': 3.0, '专业平均（折线）': 90, '个人（折线）': 80 },
        ],
        tableData:[
          // {
          //   key: '1', 
          //   sortIndex:'1', 
          //   semester: '2015学期1', 
          //   personal: '7.0', 
          //   collegeAverage: '3.9' 
          // }
        ],
        pagination: {
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getRelationInfo() {
    	let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
    	let _majorCode = JSON.parse(localStorage.getItem("studentItem")).majorCode;
        postAction(
            '/bigdata/studentScoresWeight/queryStudentStudyByStudentNo',
            {
                studentNo: _studentNo,
                majorCode: _majorCode,
            }
        ).then((res) => {
            if(res.success && res.obj.length > 0) {

                let _lineData = [];
                let _tableData = [];

                res.obj.forEach(
                    (item, index) => {

                        _lineData.push({
                            name: item.semesterYear,
                            "专业平均（柱状）": item.majAvg ? Number(Math.round(item.majAvg*100)/100) : 0,
                            "个人（柱状）": item.oneAvg ? Number(Math.round(item.oneAvg*100)/100) : 0,
                            "专业平均（折线）": item.majGpa ? Number(Math.round(item.majGpa*100)/100) : 0,
                            "个人（折线）": item.oneGpa ? Number(Math.round(item.oneGpa*100)/100) : 0
                        })

                        _tableData.push({
                            key: index + 1,
                            sortIndex: index + 1,
                            semester: item.semesterYear,
                            majAvg: item.majAvg ? Number(Math.round(item.majAvg*100)/100) : 0,
                            oneAvg: item.oneAvg ? Number(Math.round(item.oneAvg*100)/100) : 0,
                            majGpa: item.majGpa ? Number(Math.round(item.majGpa*100)/100) : 0,
                            oneGpa: item.oneGpa ? Number(Math.round(item.oneGpa*100)/100) : 0,
                        })
                    }
                )
                this.setState({
                    lineData: _lineData,
                    tableData: _tableData,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: res.obj.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    lineData: [],
                    tableData: [],
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }

    componentDidMount() {
        this.getRelationInfo()
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'source-xls')
        }
      })
    }

    //导出明细
    getDetail () {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        window.location.href=`${hostUrl}/bigdata/studentScoresWeight/exportStudentScore?studentNo=${_studentNo}&reorder=3`;
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">个人历史学业情况</div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                detailCallback={this.getDetail.bind(this)}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "个人历史学业情况"
                }}
                tableToExcel={{
                  table: 'source-xls',
                  filename: '个人历史学业情况excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    // <LineChart
                    //   data={this.state.lineData}
                    //   padding={[10, 40, 60, 60]}
                    //   height={this.props.height - 118}
                    //   colors={["#1890ff", "#f5a623"]}
                    //   onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    // />
                    <BarLineChart 
                        data={this.state.lineData} 
                        LineDataKey={["专业平均（折线）","个人（折线）"]} 
						padding={[10, 40, 60, 60]}
                        height={this.props.height - 110}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        lineScale={{
                            '专业平均（折线）':{minLimit:0,ticks:[0,1,2,3,4,5]},
                            '个人（折线）':{minLimit:0,ticks:[0,1,2,3,4,5]}
                        }}
                        geomType="intervalStack">
                    </BarLineChart>
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:280}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


