import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table } from 'antd';
import { hostUrl } from "../../../../../env";
import { postAction } from "../../../../../axios"
import { Chart, Geom, Axis, Tooltip } from "bizcharts";
import NoData from "../../../../../common/components/NoData/NoDataComponent"

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '专业',
  dataIndex: 'college',
  render: (text) => {
      return (
          <span title={text}>{text}</span>
      )
  }
}, {
  title: '平均成绩',
  dataIndex: 'scoreEverage'
},{
  title:'成绩',
  dataIndex:'score'
}];

export default class Predict extends Component {

    state = {
        tab: '1',
        chartIns: undefined,
        total: 0,
        data: [
            // { month: "专业一", "平均成绩": 3.9, "成绩": 80 }
        ],
        lineScale2: {
            "平均成绩": { ticks: [0, 20, 40, 60, 80, 100] },
            "成绩": { ticks: [0, 20, 40, 60, 80, 100] }
        },
        tableData:[
            // {
            //     key: '1', 
            //     sortIndex:'1', 
            //     date: 'date', 
            //     late: 'late', 
            //     Nolate: 'Nolate' 
            // }
        ],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    //成绩预测（接口已重新写1129）
    getRelationInfo() {
    	let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
    	let _majorCode = JSON.parse(localStorage.getItem("studentItem")).majorCode;

        postAction(
            '/bigdata/studentScoreForecast/getStudentScoreForecast',
            {
                studentNo: _studentNo,
                majorCode: _majorCode,
            }
        ).then((res) => {
            if (res.success) {
            	if (res.obj && res.obj.length > 0) {
            		let newData = [],newTable = []
            		res.obj.map(item => (
            		    newData.push({
            		        month:item.courseCode,
            		        平均成绩:Math.round(item.avgVal*100)/100,
            				成绩:Math.round(item.forecastScore*100)/100
            		    })
            		))
                    res.obj.map((item,index) => (
                        newTable.push({
                            key: index+1, 
                            sortIndex:index+1, 
                            college:item.courseCode, 
                            scoreEverage: Math.round(item.avgVal*100)/100,
                            score: Math.round(item.forecastScore*100)/100
                        })
                    ))
                    
            		this.setState({
            		    data:newData,
                        tableData:newTable,
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize:5, // 每页条数
                            showQuickJumper: true,
                            total: newTable.length,
                            showTotal: (total) => {
                              return (
                                `共${total}条数据`
                              )
                            }
                        }
            		})
            	} else {
            		this.setState({
            		    data:[],
                        tableData:[],
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize:5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                              return (
                                `共${total}条数据`
                              )
                            }
                        }
            		})
            	}
            }
        })
    }

    componentDidMount() {
        this.getRelationInfo()
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            }
        )
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'yuce-xls')
        }
      })
    }

    //导出明细
    getDetail() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        window.location.href = `${hostUrl}/bigdata/score/exportForecastDetail?studentNo=${_studentNo}`;
    }

    render() {

        const label = {
            offset: 10, // 数值，设置坐标轴文本 label 距离坐标轴线的距离
            textStyle: {
                textAlign: 'center', // 文本对齐方向，可取值为： start center end
            },
            formatter (text, item, index) {
                let str = text.substr(0, 5) + "..."
                return str
            }
        }

        const { data, lineScale2 } = this.state
        //处理显示平均成绩折线图
        let fields = [], nameKey
        if (data && data[0]) {
            nameKey = Object.keys(data[0])[0];
            Object.keys(data[0]).forEach((item, index) => {
                if (index !== 0 && item !== '成绩') {
                    fields.push(item);
                }
            })
        }

        return (
            <MainCard
                title={<div className="common-title">本学期成绩预测<span>单位：人</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                detailCallback={this.getDetail.bind(this)}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "本学期成绩预测"
                }}
                tableToExcel={{
                  table: 'yuce-xls',
                  filename: '本学期成绩预测excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                        this.state.data && this.state.data.length > 0
                        ?
                        <Chart 
                            padding={[20, 40, 50, 60]}
                            height={this.props.height - 118}
                            data={data} 
                            scale={lineScale2} 
                            forceFit 
                            onGetG2Instance={chartIns => {
                                this.setState({chartIns:chartIns})
                            }}
                        >
                            <Axis name="month" label={label}/>
                            <Axis name="成绩"/>
                            {/* 不显示右边的y轴 */}
                            {
                                fields.map((item, index) => (
                                    <Axis key={index} name={item} visible={false}/>
                                ))
                            }
                            <Tooltip crosshairs={{type: "y"}}/>
                            <Geom type="interval" position="month*成绩"
                                  color={['成绩', adv => adv < 60 ? '#f36464' : '#4a90e2']}
                                  tooltip={['month*成绩', (month, 成绩) => {
                                      if (成绩 < 60) {
                                          成绩 = 成绩 + '(不及格)'
                                      } else {
                                          成绩 = 成绩 + '(及格)'
                                      }
                                      return {
                                          name: '成绩',
                                          title: month,
                                          value: 成绩
                                      };
                                  }]}
                            />
                            {/* 折线图 */}
                            {
                                fields.map((item, i) => (
                                    <React.Fragment key={i}>
                                        <Geom
                                            type="line"
                                            position={nameKey + "*" + item}
                                            color={'#f6ac31'} tooltip={['month*平均成绩', (month, 平均成绩) => {
                                            return {
                                                name: '平均成绩',
                                                title: month,
                                                value: 平均成绩
                                            };
                                        }]}/>
                                        <Geom
                                            type="point"
                                            position={nameKey + "*" + item}
                                            shape={'circle'}
                                            color={'#f6ac31'} tooltip={['month*平均成绩', (month, 平均成绩) => {
                                            return {
                                                name: '平均成绩',
                                                title: month,
                                                value: 平均成绩
                                            };
                                        }]}/>
                                    </React.Fragment>
                                ))
                            }
                        </Chart>
                        :
                        <NoData height={290} data={'nodata'}/>
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="middle"
                        pagination={this.state.pagination}
                        onChange={this.handleTableChange}
                        style={{height:290}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


