import React, { Component, Fragment } from 'react';
import { Row, Col } from 'antd';

import PeoplePortrait from "./PeoplePortrait";
import Guiji from "./Guiji";
import GerenGuanxi from "./GerenGuanxi";


export default class Huaxiang extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col span={24}>
                        <PeoplePortrait />
                    </Col>

                    <Col span={24} className="card-padding">
                        <Guiji height={500} />
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <GerenGuanxi />
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


