import React, { Component, Fragment } from 'react';
import { Row, Col } from 'antd';
import LibrtryCard from "./LibrtryCard";
import Classification from "./Classification";
import BookList from "./BookList";
import Trend from "./Trend";


export default class Librtry extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col span={24} className="padding-20">
                        <LibrtryCard />
                    </Col>

                    <Col span={12} className="card-padding card-right">
                        <Classification />
                    </Col>

                    <Col span={12} className="card-padding card-left">
                        <BookList />
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <Trend height={400} />
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


