import React, { Component } from 'react';
import { Row, Col, Card, Popover, Icon  } from 'antd';
import { postAction } from "../../../../../axios"
import boyPng from '../../../../../assets/img/boyPng.png';
import girlPng from '../../../../../assets/img/girlPng.png';


export default class PeoplePortrait extends Component {

    state = {
        data: {
            personalInfo: {
                studentName: '',
                gender: '',
                hometown: '',
                birthday: '',
                age: '',
            }
        },
        internetInfo: [],  // 上网习惯
        studyInfo: [],     // 学习情况
        consumeInfo: [],   // 消费水平
        identity:[],       // 身份特点
        psychological:[],  // 心理预警
        situation: [],     // 资助情况
    }

    getStudentDetail() {
    	let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
    	let data = { studentNo: _studentNo };
        postAction(
            '/bigdata/portrait/getPersonalLabel',data
        ).then((res) => {
            if (res.success) {
                if(res.obj !== '' && res.obj.studentInfo !== ''){
                    this.setState({
                        data: {
                            personalInfo: {
                                studentName: res.obj.studentInfo.name,
                                gender: res.obj.studentInfo.gender,
                                hometown: res.obj.studentInfo.nativePlace,
                                birthday: res.obj.studentInfo.birthday,
                                politicalStatus: res.obj.studentInfo.politicalStatus
                            }
                        },
                        studyInfo: res.obj.studyInfo,
                        internetInfo: res.obj.internetInfo,
                        identity: res.obj.identifyInfo,
                        consumeInfo: res.obj.consumeInfo,

                    })
                }else{
                    this.setState({
                        data: {
                            personalInfo: {
                                studentName: '',
                                gender: '',
                                hometown: '',
                                birthday: '',
                                age: '',
                            }
                        },
                        studyInfo: [],
                        internetInfo: [],
                        identity: [],
                        consumeInfo: [],
                    })
                }
            }
        })

        // 资助情况
        postAction(
            "/bigdata/povertyAnalyze/subsidizeTarget",data
        ).then(
            (res) => {
                if (res.success) {
                    if (res.obj && res.obj.isTarget) {
                        this.setState({
                            situation: [{ labelName: res.obj.isTarget}]
                        })
                    }
                } else {
                    this.setState({
                        situation:[]
                    })
                }
            }
        )

        // 心理预警
        postAction(
            "/bigdata/mentality/getCurrentAlarm",data
        ).then(
            (res) => {
                if (res.success) {
                    if (res.obj && res.obj.length > 0) {
                        this.setState({
                            psychological:[{ labelName: res.obj}]
                        })
                    }
                } else {
                    this.setState({
                        psychological:[]
                    })
                }
            }
        )
    }

    componentDidMount() {
        this.getStudentDetail()
    }
    

    render() {

        // 学习情况
        const learning = (
            <div>
                <p style={{margin:"0px"}}>学习情况标签是根据本学年的数据情况计算</p>
            </div>
        );


        // 上网习惯
        const Internet = (
            <div>
                <p style={{margin:"0px"}}>上网习惯标签是根据本年度的数据情况计算</p>
            </div>
        );

        // 身份特点
        const specialty = (
            <div>
                <p style={{margin:"0px"}}>特招生：在入学已经确定</p>
                <p style={{margin:"0px"}}>困难生：每个学年都会评定</p>
            </div>
        );

        // 消费水平
        const consumption = (
            <div>
                <p style={{margin:"0px"}}>消费水平标签是根据近一年的消费数据情况计算</p>
            </div>
        );

        // 心理健康
        const health = (
            <div>
                <p style={{margin:"0px"}}>心理健康标签是根据近三个月的数据情况计算</p>
            </div>
        );

        // 资助情况
        const funding = (
            <div>
                <p style={{margin:"0px"}}>资助情况标签是根据近本学年的数据情况计算</p>
            </div>
        );

        const Genderpic = this.state.data.personalInfo.gender && this.state.data.personalInfo.gender === '男性' ? boyPng : girlPng;
        const { personalInfo: { studentName, gender, hometown, birthday, politicalStatus } } = this.state.data
        const { internetInfo, studyInfo, consumeInfo, identity, psychological, situation } = this.state
        return (
            <Row>
                <Col span={5} className="portrait-img" style={{ marginTop: '9%', marginLeft: '8%' }}>
                    <img src={Genderpic} alt="" />
                </Col>

                <Col span={15} style={{ marginTop: '3%' }}>
                    <div className="blackboard">
                        <Card>
                            <div className="card-left">
                                <i className="iconfont icon-ziliaoguanli" />
                            </div>
                            <div className="card-right">
                                <p>姓名：<span>{studentName}</span></p>
                                <p>性别：<span>{gender}</span></p>
                                <p>籍贯：<span>{hometown}</span></p><br />
                                <p>生日：<span>{birthday}</span></p>
                                <p>政治面貌：<span>{politicalStatus}</span></p>
                            </div>
                        </Card>

                        {/*学习情况*/}
                        <div style={{width:800, height: 220,marginTop:25,position:'relative'}}>
                            <div style={{width:380,float:"left"}}>
                                <ul className="study" style={{top:30}}>
                                    <li className="line"/>
                                    <li className="title">学习情况
                                        <Popover content={learning} title="学习情况标签定义">
                                            <Icon type="exclamation-circle" style={{fontSize:"14px", color:"#fff",marginLeft:5, cursor: "pointer"}} />
                                        </Popover>
                                    </li>
                                    <li className="line"/>
                                </ul>
                                <div style={{height:140,position:'absolute',top:45,width:200,left:40,overflow:'hidden'}}>
                                    {
                                        studyInfo && studyInfo.map(({ labelNo, labelName }, index) => (
                                            <p style={{ color:'#fff',padding:4,border:'1px solid #fff',textAlign:'center',width:'8rem',marginLeft:'3.5rem',marginTop:10,borderRadius:6,background:'rgba(255,255,255,0.2)' }} key={index}>{labelName}</p>
                                        ))
                                    }
                                </div>
                            </div>

                            {/*上网习惯*/}
                            <div style={{width:380,float:"left", marginLeft:"31.5%"}}>
                                <ul className="internet" style={{top:30}}>
                                    <li className="line" style={{width:60}}/>
                                    <li className="title">上网习惯
                                        <Popover content={Internet} title="上网习惯标签定义">
                                            <Icon type="exclamation-circle" style={{fontSize:"14px", color:"#fff",marginLeft:5, cursor: "pointer"}} />
                                        </Popover>
                                    </li>
                                    <li className="line" style={{width:60}}/>
                                </ul>
                                <div style={{height:140,position:'absolute',top:45,width:200,left:"38%",overflow:'hidden'}}>
                                    {
                                        internetInfo && internetInfo.map(({ labelNo, labelName }, index) => (
                                            <p style={{ color:'#fff',padding:4,border:'1px solid #fff',textAlign:'center',width:'8rem',marginLeft:'2.8rem',marginTop:10,borderRadius:6,background:'rgba(255,255,255,0.2)' }} key={index}>{labelName}</p>
                                        ))
                                    }
                                </div>
                            </div>

                            {/*身份特点*/}
                            <div style={{width:380,float:"left", marginLeft:"63%"}}>
                                <ul className="internet" style={{top:30}}>
                                    <li className="line" style={{width:60}}/>
                                    <li className="title">身份特点
                                        <Popover content={specialty} title="身份特点标签定义">
                                            <Icon type="exclamation-circle" style={{fontSize:"14px", color:"#fff",marginLeft:5, cursor: "pointer"}} />
                                        </Popover>
                                    </li>
                                    <li className="line" style={{width:60}}/>
                                </ul>
                                <div style={{height:140,position:'absolute',top:45,width:200,right:32,overflow:'hidden'}}>
                                    {
                                        identity && identity.map(({ labelNo, labelName }, index) => (
                                            <p style={{ color:'#fff',padding:4,border:'1px solid #fff',textAlign:'center',width:'8rem',marginLeft:'2rem',marginTop:10,borderRadius:6,background:'rgba(255,255,255,0.2)' }} key={index}>{labelName}</p>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>

                        {/*消费水平*/}
                        <div style={{width:800, height: 220,position:'relative'}}>
                            <div style={{width:380,float:"left"}}>
                                <ul className="study" style={{top:0}}>
                                    <li className="line"/>
                                    <li className="title">消费水平
                                        <Popover content={consumption} title="消费水平标签定义">
                                            <Icon type="exclamation-circle" style={{fontSize:"14px", color:"#fff",marginLeft:5, cursor: "pointer"}} />
                                        </Popover>
                                    </li>
                                    <li className="line"/>
                                </ul>
                                <div style={{height:120,position:'absolute',top:8,width:200,left:40,overflow:'hidden'}}>
                                    {
                                        consumeInfo && consumeInfo.map(({ labelNo, labelName }, index) => (
                                            <p style={{ color:'#fff',padding:4,border:'1px solid #fff',textAlign:'center',width:'8rem',marginLeft:'3.5rem',marginTop:10,borderRadius:6,background:'rgba(255,255,255,0.2)' }} key={index}>{labelName}</p>
                                        ))
                                    }
                                </div>
                            </div>

                            {/*心理预警*/}
                            <div style={{width:380,float:"left", marginLeft:"31.5%"}}>
                                <ul className="internet" style={{top:0}}>
                                    <li className="line" style={{width:60}}/>
                                    <li className="title">心理预警
                                        <Popover content={health} title="心理预警标签定义">
                                            <Icon type="exclamation-circle" style={{fontSize:"14px", color:"#fff",marginLeft:5, cursor: "pointer"}} />
                                        </Popover>
                                    </li>
                                    <li className="line" style={{width:60}}/>
                                </ul>
                                <div style={{height:120,position:'absolute',top:8,width:200,left:"38%",overflow:'hidden'}}>
                                    {
                                        psychological && psychological.map(({ labelNo, labelName }, index) => (
                                            <p style={{ color:'#fff',padding:4,border:'1px solid #fff',textAlign:'center',width:'8rem',marginLeft:'2.8rem',marginTop:10,borderRadius:6,background:'rgba(255,255,255,0.2)' }} key={index}>{labelName}</p>
                                        ))
                                    }
                                </div>
                            </div>

                            {/*资助情况*/}
                            <div style={{width:380,float:"left",marginLeft:"63%"}}>
                                <ul className="internet" style={{top:0}}>
                                    <li className="line" style={{width:60}}/>
                                    <li className="title">资助情况
                                        <Popover content={funding} title="资助情况标签定义">
                                            <Icon type="exclamation-circle" style={{fontSize:"14px", color:"#fff",marginLeft:5, cursor: "pointer"}} />
                                        </Popover>
                                    </li>
                                    <li className="line" style={{width:60}}/>
                                </ul>
                                <div style={{height:120,position:'absolute',top:8,width:200,right:31,overflow:'hidden'}}>
                                    {
                                        situation && situation.map(({ labelNo, labelName }, index) => (
                                            <p style={{ color:'#fff',padding:4,border:'1px solid #fff',textAlign:'center',width:'8rem',marginLeft:'2rem',marginTop:10,borderRadius:6,background:'rgba(255,255,255,0.2)' }} key={index}>{labelName}</p>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>
        )
    }
}


