import React, { Component, Fragment } from 'react';
import { Row, Col } from 'antd';
import PriceIndex from "./PriceIndex";
import ThreeMeals from "./ThreeMeals/ThreeMeals";
import Structure from "./Structure";
import Place from "./Place";
import Regular from "./Regular";
// import DietTypes from "./DietTypes";
import ConsumerTrends from "./ConsumerTrends";


export default class Xiaofei extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    {/* 本学年三餐平均消费情况 */}
                    <Col span={24} className="card-padding">
                        <PriceIndex height={400} />
                    </Col>

                    {/* 近七天三餐就餐情况 */}
                    <Col span={12} className="card-padding card-right">
                        <ThreeMeals height={400} />
                    </Col>

                    {/* 近三个月消费结构 */}
                    <Col span={12} className="card-padding card-left">
                        <Structure height={400} />
                    </Col>

                    {/* 本学年刷卡地点分布TOP5 */}
                    <Col span={12} className="card-padding card-right">
                        <Place height={400} />
                    </Col>

                    {/* 本学期三餐规律度 */}
                    <Col span={12} className="card-padding card-left">
                        <Regular height={400} />
                    </Col>

                    {/* 本年度三餐饮食类型分析 */}
                    {/*<Col span={12} className="card-padding card-left">*/}
                        {/*<DietTypes height={400} />*/}
                    {/*</Col>*/}

                    {/* 本学年消费趋势 */}
                    <Col span={24} className="card-padding card-bottom">
                        <ConsumerTrends height={400} />
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


