import React, { Component, Fragment } from 'react';
import { Row, Col } from 'antd';

import History from "./History";
// import OnTime from "./OnTime";
import Predict from "./Predict";
// import Taoke from "./Taoke";
import Paiming from "./Paiming";
import PersonalInterface from "./PersonalInterface"
// import CourseSituation from "./CourseSituation"


export default class Xueye extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col span={24} className="card-padding">
                        <p style={{backgroundColor:"#fff",marginBottom:"0px",fontSize:"16px",fontWeight:600, padding:"20px"}}>仅作为学生管理者判断学生学业状况的一个参考数据，不作为任何评奖评优或其他工作的评价指标！辅修、通识、国外学习课程类、非本专业教学计划要求的课程，不纳入统计。</p >
                    </Col>
                    {/*个人历史成绩分析*/}
                    <Col span={24} className="card-padding">
                        <History height={400} />
                    </Col>

                    {/*准时上课情况*/}
                    {/*<Col span={12} className="card-padding card-left">*/}
                        {/*<OnTime height={400} />*/}
                    {/*</Col>*/}

                    {/*本学期成绩预测*/}
                    <Col span={24} className="card-padding">
                        <Predict height={400} />
                    </Col>

                    {/*本学期疑似逃课分析*/}
                    {/*<Col span={12} className="card-padding card-left">*/}
                        {/*<Taoke height={400} />*/}
                    {/*</Col>*/}

                    {/*个人成绩排名*/}
                    <Col span={24} className="card-padding">
                        <Paiming />
                    </Col>

                    {/*个人课表情况*/}
                    <Col span={24} className="card-padding">
                        <PersonalInterface />
                    </Col>

                    {/* <Col span={24} className="card-padding card-bottom">
                        <CourseSituation />
                    </Col> */}
                </Row>
            </Fragment>
        )
    }
}


