import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table} from 'antd';
import { postAction } from "../../../../../axios"
import LineChart from "../../../../../common/components/BizCharts/LineChart";

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '月份',
  dataIndex: 'month',
},{
  title:'本人',
  dataIndex:'myself'
},{
  title:'全校平均',
  dataIndex:'school'
},{
  title:'全校男生平均',
  dataIndex:'schoolBoy'
},{
  title:'全校女生平均',
  dataIndex:'schoolGirl'
}];
export default class ConsumerTrends extends Component {

    state = {
        lineData: [],
        tab: '1',
        chartIns: undefined,
        total: 0,
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getPerConsumeTrend() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        postAction(
            '/bigdata/consume/getPerConsumeTrend',
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if (res.success) {
                let dataObj = [],newTable = []
                res.obj.forEach(element => {
                    dataObj.push({
                        "month": element.month + '月',
                        "本人": element.本人,
                        "全校平均": element.全校平均,
                        "全校男生平均": element.全校男生平均,
                        "全校女生平均": element.全校女生平均
                    })
                })
                res.obj.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        month: element.month + '月', 
                        myself: element.本人+'元',
                        school: element.全校平均+'元',
                        schoolBoy: element.全校男生平均+'元',
                        schoolGirl: element.全校女生平均+'元'
                    })
                });
                this.setState({
                    lineData: dataObj,
                    tableData: newTable,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'money-xls')
        }
      })
    }

    componentDidMount() {
        this.getPerConsumeTrend()
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">本学年消费趋势<span>单位：元</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "本学年消费趋势"
                }}
                tableToExcel={{
                  table: 'money-xls',
                  filename: '本学年消费趋势excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    <LineChart
                        data={this.state.lineData}
                        height={this.props.height - 115}
                        padding={[10, 40, 60, 60]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        colors={["#1890ff", "#FACC14","#2FC25B","#F04864"]}
                    />
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:290}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


