import React, { Component } from 'react';
import { Card, Table,Row,Col } from 'antd';
import { postAction } from "../../../../../axios"


const columns2 = [{
	title: '序号',
	dataIndex: 'index',
	align: 'center',
}, {
	title: '课程名称',
	dataIndex: 'lesson',
	align: 'center',
	render: (text) => {
		return (
				<span title={text}>{text}</span>
		)
	}
}, {
	title: '教师',
	dataIndex: 'teacher',
	align: 'center',
}, {
	title: '学分',
	dataIndex: 'credit',
	align: 'center',
}, {
	title: '上课周数',
	dataIndex: 'week',
	align: 'center',
}]
export default class PersonalInterface extends Component {

  constructor(props) {
    super(props)
    this.state = {
        data: [
          //   {
          //       key: '1',
          //       time: '上午',
          //       section: '1-2节',
          //       monday: '周一',
          //       tuesday: '周二',
          //       wednesday: '投资课程<br/>1-3节<br/>301教师',
          //       thursday: '周四',
          //       friday: '周五',
          //       saturday: '周六',
          //       sunday: '周天'
          // }
        ],
        // semester:'',
        dataClass: [
            // {
            //   key: '1',
            //   index: '1',
            //   lesson: '公共课',
            //   teacher: '小米',
            //   credit: 2.4,
            //   week: '1-8周',
            // }
        ],
        pagination: {
            current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        }
    }
  }

    //更换学期
    // onSemester = (e) => {
    //     this.setState(
    //         {
    //             semester: e
    //         },
    //         () => {
    //             this.getNoSemester()
    //             this.getNoSemesterClass()
    //         }
    //     )
    // }

  //个人课表情况(接口已重新写1129)
  getNoSemester() {
      let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
    postAction(
      '/bigdata/studentTimeTable/findAll',
      {
        studentNo: _studentNo,
        // semester: this.state.semester,
        // pageInfo:{
        //    pageNum:this.state.pagination.current,
        //    pageSize:this.state.pagination.pageSize
        // }
      }
    ).then((res) => {
        if (res.success && res.obj.length > 0) {
            let newData = [];
            res.obj.forEach(
            (item, index) => {
              newData.push({
                  key: (index + 1),
                  time: item.timeInterval,
                  section: item.nodeNumber,
                  monday: item.weekMonday,
                  tuesday: item.weekTuesday,
                  wednesday: item.weekWednesday,
                  thursday: item.weekThursday,
                  friday: item.weekFriday,
                  saturday: item.weekSaturday,
                  sunday: item.weekSunday
              })
                this.setState({
                    data: newData
                })
            }
            )
        } else {
            this.setState({
                data: []
            })
        }
    })
  }

  //课程情况接口已重写1130
  getNoSemesterClass() {
    let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
    postAction(
      '/bigdata/score/queryTimeTableByStudentNoSemester',
      {
        studentNo: _studentNo,
        // semester: this.state.semester,
        pageInfo:{
              pageNum:this.state.pagination.current,
              pageSize:this.state.pagination.pageSize
        }
      }
    ).then((res) => {
      if (res.success) {
          if (res.obj.list && res.obj.list.length > 0) {
            let newData = [];
            res.obj.list.forEach(
                (item, index) => {
                  newData.push({
                      key: (index + 1),
                      index: (index + 1),
                      lesson: item.course_name,
                      teacher: item.course_teacher,
                      credit: item.xf,
                      week: item.course_week
                  })
                    this.setState({
                        dataClass: newData,
                        pagination: {
                            current: res.obj.pageNum, // 当前页数，
                            pageSize: res.obj.pageSize, // 每页条数
                            total: res.obj.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            )
          } else {
              this.setState({
									dataClass: [],
                  pagination: {
                      current: 1, // 当前页数，
                      pageSize: 10, // 每页条数
                      total: 0, // 数据总条数
                      showTotal: (total) => `共${total}条数据`, //显示数据总条数
                      showQuickJumper: true // 显示页码快速跳转
                  }
              })
          }
      }
    })
  }

  handleTableChange = (pagination) => {
    this.setState(
        () => {
            return {
                pagination: {
                    current: pagination.current, // 当前页数，
                    pageSize: pagination.pageSize, // 每页条数
                    total: pagination.total, // 数据总条数
                    showTotal: (total) => `共${total}条数据`,
                    showQuickJumper: true // 显示页码快速跳转
                }
            }
        },
        () => {
            this.getNoSemesterClass();
        }
    )
  }
	
  componentDidMount() {
    this.getNoSemester()
    this.getNoSemesterClass()
  }

  render() {

    const columns = [{
      title: '上午/下午',
      dataIndex: 'time',
      align: 'center',
    }, {
      title: '节数',
      dataIndex: 'section',
      align: 'center',
    }, {
      title: '星期一',
      dataIndex: 'monday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }, {
      title: '星期二',
      dataIndex: 'tuesday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }, {
      title: '星期三',
      dataIndex: 'wednesday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }, {
      title: '星期四',
      dataIndex: 'thursday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }, {
      title: '星期五',
      dataIndex: 'friday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }, {
      title: '星期六',
      dataIndex: 'saturday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }, {
      title: '星期日',
      dataIndex: 'sunday',
      align: 'center',
      render: (text)=>{
          return (<div dangerouslySetInnerHTML={{__html: text}}></div>)
      }
    }];

    return (
      <Card
          title={<div className="common-title">本学期个人课表情况</div>}
          headStyle={{ border: 'none' }}
          bodyStyle={{ paddingTop: 0 }}
          // extra={
          //     <Select defaultValue="全部" style={{ width: 180 }} onChange={this.onSemester}>
          //         <Option value="1">上学期</Option>
          //         <Option value="2">下学期</Option>
          //     </Select>
          // }
      >
        <Row>
            <Col span={7} style={{paddingRight:15}}>
                <Table
                    columns={columns2}
                    dataSource={this.state.dataClass}
                    bordered
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                    size="middle"
              />
            </Col>
            <Col span={17}>
                <Table columns={columns} dataSource={this.state.data} bordered size="middle" />
            </Col>
        </Row>
        
      </Card>
    )
  }
}


