import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table } from 'antd';
import { postAction } from "../../../../../axios"
import LineChart from "../../../../../common/components/BizCharts/LineChart";
import {hostUrl} from "../../../../../env";

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '月份',
  dataIndex: 'month',
  render: (text) => {
      return (
          <span title={text}>{text}</span>
      )
  }
}, {
  title: '早餐(元)',
  dataIndex: 'breakfast'
},{
  title:'中餐(元)',
  dataIndex:'lunch'
}, {
    title: '晚餐(元)',
    dataIndex: 'supper'
},{
    title:'夜餐(元)',
    dataIndex:'midnight'
},{
    title:'合计(元)',
    dataIndex:'total'
}];

export default class PriceIndex extends Component {

    state = {
        tab: '1',
        chartIns: undefined,
        total: 0,
        lineData: [],
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getConsumerIndex() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        postAction(
            '/bigdata/consume/getAvgMealsTrend',
            {
                studentNo:  _studentNo
            }
        ).then((res) => {
            if (res.success) {
                let dataObj = [],newTable = []
                res.obj.forEach(element => {
                    dataObj.push({
                        month: element.month + '月',
                        "早餐": element.breakfast,
                        "中餐": element.lunch,
                        "晚餐": element.supper,
                        "夜餐": element.midnight,
                        "合计": (element.breakfast + element.lunch + element.supper + element.midnight).toFixed(2)
                    })
                });
                res.obj.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        month: element.month + '月',
                        breakfast: element.breakfast,
                        lunch: element.lunch,
                        supper: element.supper,
                        midnight: element.midnight,
                        total: (element.breakfast + element.lunch + element.supper + element.midnight).toFixed(2)
                    })
                });
                this.setState({
                    lineData: dataObj,
                    tableData:newTable,
                    pagination: {
                      pageSize:5, // 每页条数
                      showQuickJumper: true,
                      total: dataObj.length,
                      showTotal: (total) => {
                        return (
                          `共${total}条数据`
                        )
                      }
                    }
                })
            }
        })
    }

    componentDidMount() {
        this.getConsumerIndex()
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'consumption-xls')
        }
      })
    }

    //导出明细
    getDetail () {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        window.location.href=`${hostUrl}/bigdata/consume/exportConsumeMealsInfo?studentNo=${_studentNo}`;
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">本学年三餐平均消费情况<span>单位：元</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                detailCallback={this.getDetail}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "本学年三餐平均消费情况"
                }}
                tableToExcel={{
                  table: 'consumption-xls',
                  filename: '本学年三餐平均消费情况excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    <LineChart
                        data={this.state.lineData}
                        padding={[10, 40, 60, 60]}
                        height={this.props.height - 100}
                        colors={["#1890ff", "#FACC14","#2FC25B","#F04864","#5ddfcf"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:290}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
            </MainCard>
        )
    }
}


