import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table, Card} from 'antd';
import { postAction } from "../../../../../axios"
import NoData from "../../../../../common/components/NoData/NoDataComponent"
import { Chart, Geom, Axis, Tooltip, Coord } from 'bizcharts';

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '个人',
  dataIndex: 'personal',
},{
  title:'专业平均',
  dataIndex:'colloge'
}];
export default class Regular extends Component {

    state = {
        data: [
            // { country: '个人', '类别': 0.6 },
            // { country: '专业平均', '类别': 0.3 }
        ],
        tab: '1',
        chartIns: undefined,
        total: 0,
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getRegularData() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        postAction(
            '/bigdata/consume/dietRegularData',
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if (res.success && res.obj !== '') {
                let personRegular = res.obj.personRegular
                let majorRegular = res.obj.majorRegular
                this.setState({
                    data: [
                        { country: '个人', '类别': personRegular },
                        { country: '专业平均', '类别': majorRegular }
                    ],
                    tableData:[
                        {
                            key: 1, 
                            sortIndex:1, 
                            personal: personRegular, 
                            colloge: majorRegular
                        }
                    ],
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: 1,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'guilv-xls')
        }
      })
    }

    componentDidMount() {
        this.getRegularData()
    }

    render() {
        const label = {
            formatter: (text) => {
                if (Number(text) <= 0.2) {
                    return ['不规律']
                } if (Number(text) <= 0.4) {
                    return ['较不规律']
                } if (Number(text) <= 0.6) {
                    return ['一般']
                } if (Number(text) <= 0.8) {
                    return ['较规律']
                } else {
                    return ['很规律']
                }
            }
        }

        const scale = {
            '类别': {
                min: 0, // 定义数值范围的最小值
                max: 1, // 定义数值范围的最大值
                ticks: [0.2, 0.4, 0.6, 0.8, 1], // 用于指定坐标轴上刻度点的文本信息（label），当用户设置了 ticks 就会按照 ticks 的个数和文本来显示。
            }
        };

        if (this.state.data && this.state.data.length > 0 && (this.state.data[0].类别 || this.state.data[1].类别)) {
            return (
                <MainCard
                    title={<div className="common-title">本学期三餐规律度</div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    downloadChart={{
                      chartIns: this.state.chartIns,
                      title: "本学期三餐规律度"
                    }}
                    tableToExcel={{
                      table: 'guilv-xls',
                      filename: '本学期三餐规律度excel下载'
                    }}
                  >
                    {
                      this.state.tab === '1'
                        ?
                        <Chart
                            height={this.props.height - 100}
                            scale={scale}
                            padding={[0, 40, 40, 60]}
                            data={this.state.data}
                            forceFit
                            onGetG2Instance={(chartIns)=> {this.setState({ chartIns })}}
                        >
                            <Coord transpose />
                            <Axis name="country" label={{ offset: 12 }} />
                            <Axis name="类别" label={label} />
                            <Tooltip />
                            <Geom type="interval" position="country*类别" color={['country', ['#1890ff', '#f5a623']]} />
                        </Chart>
                        :
                        <div>
                          <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="small"
                            pagination={this.state.pagination}
                            style={{height:290}}
                          />
                          <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                        </div>
                    }
                </MainCard>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title">本学期三餐规律度</div>}
                    headStyle={{ border: 'none' }}
                >
                    <NoData height={300} data={'nodata'} />
                </Card>
            )
        }
    }
}


