import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table } from 'antd';
import { postAction } from "../../../../../axios"
import LineChart from "../../../../../common/components/BizCharts/LineChart"
// import NoData from '@/common/components/NoData/NoDataComponent';


const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '日期',
  dataIndex: 'month',
},{
  title:'本人',
  dataIndex:'myself'
},{
  title:'班级平均',
  dataIndex:'class'
}];
export default class TimeLong extends Component {

    state = {
        lineData: [
            // { month: "10.1", "本人": 7.0, "班级平均": 3.9 }
        ],
        tab: '1',
        chartIns: undefined,
        total: 0,
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        }
    }

    getSurfDuration() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        postAction(
            '/bigdata/netTime/surfDurationLastSeven',
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if (res.success) {
                let dataObj = [],newTable = []
                res.obj.forEach(element => {
                    dataObj.push({
                        month:element.surfDate,
                        本人:element.oneselfDuration,
                        班级平均:element.classDuration
                    })
                })
                res.obj.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        month: element.surfDate, 
                        myself: element.oneselfDuration+'小时',
                        class: element.classDuration+'小时',
                    })
                })
                this.setState({
                    lineData:dataObj,
                    tableData: newTable,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'qushi-xls')
        }
      })
    }

    componentDidMount() {
        this.getSurfDuration()
    }

    render() {

        // if (this.state.lineData && this.state.lineData[0] && this.state.total > 0) {
            return (
                <MainCard
                    title={<div className="common-title">上网时长趋势（最近7天）<span>单位：小时</span></div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    downloadChart={{
                        chartIns: this.state.chartIns,
                        title: "上网时长趋势（最近7天）"
                    }}
                    tableToExcel={{
                        table: 'qushi-xls',
                        filename: '上网时长趋势（最近7天）excel下载'
                    }}
                >
                    {
                        this.state.tab === '1'
                            ?
                            <LineChart
                                data={this.state.lineData}
                                padding={[10, 40, 60, 60]}
                                height={this.props.height - 115}
                                colors={['#f5a623', '#65a1e7']}
                                onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            />
                            :
                            <div>
                                <Table
                                    dataSource={this.state.tableData}
                                    columns={columns}
                                    bordered
                                    size="small"
                                    pagination={this.state.pagination}
                                    style={{height:290}}
                                />
                                <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                            </div>
                    }
                </MainCard>
            )
        // } else {
        //     return (
        //         <Card
        //             title={<div className="common-title">上网时长趋势（最近7天）<span>单位：小时</span></div>}
        //             headStyle={{ border: "none" }}
        //             bodyStyle={{ padding: 0 }}
        //
        //         >
        //             <NoData height={338} data={'nodata'} />
        //         </Card>
        //     )
        // }


    }
}


