import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import MainCard from "@/common/components/MainCard"
import { Table } from 'antd';
import { postAction } from "../../../../../axios"
import PieChart from "../../../../../common/components/BizCharts/PieChart";

const columns = [{
  title:'序号',
  dataIndex: 'sortIndex',
  width: '10%',
},{
  title: '类型',
  dataIndex: 'type',
  render: (text) => {
      return (
          <span title={text}>{text}</span>
      )
  }
},{
  title:'金额',
  dataIndex:'percentage'
}];

export default class Structure extends Component {

    state = {
        tab: '1',
        chartIns: undefined,
        total: 0,
        pieData: [
            // { item: '计算机', count: 40 },
        ],
        tableData:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize:5, // 每页条数
            showQuickJumper: true,
            total: null,
            showTotal: (total) => {
              return (
                `共${total}条数据`
              )
            }
        },
        engel:0
    }

    getConsumeStructure() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        postAction(
            '/bigdata/consume/getConsumeStructure',
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if (res.success) {
                let dataObj = [],newTable = []
                res.obj.detail && res.obj.detail.forEach(element => {
                    dataObj.push({
                        item: element.name,
                        count: element.value
                    })
                });
                res.obj.detail && res.obj.detail.forEach((element,index) => {
                    newTable.push({
                        key: index+1, 
                        sortIndex:index+1, 
                        type: element.name, 
                        percentage: element.value.toFixed(2)+'元'
                    })
                });
                this.setState({
                    pieData: dataObj,
                    tableData:newTable,
                    engel:res.obj.engel ? Number(res.obj.engel.engel * 100) : "0" ,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: newTable.length,
                        showTotal: (total) => {
                          return (
                            `共${total}条数据`
                          )
                        }
                    }
                })
            } else {
                this.setState({
                    pieData: [],
                    tableData:[],
                    engel:0,
                    pagination: {
                        current: 1, // 当前页数，
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }
    
    //tab切换
    changeTab = (e) => {
      this.setState({ tab: e.target.value }, () => {
        if (this.refs['table']) {
          const tableCon = ReactDOM.findDOMNode(this.refs['table'])
          const table = tableCon.querySelector('table')
          table.setAttribute('id', 'threeMoney-xls')
        }
      })
    }

    componentDidMount() {
        this.getConsumeStructure()
    }

    render() {
        return (
            <MainCard
                title={<div className="common-title">近三个月消费结构<span>单位：元</span></div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                // detailCallback={this.mingxi}
                downloadChart={{
                  chartIns: this.state.chartIns,
                  title: "近三个月消费结构"
                }}
                tableToExcel={{
                  table: 'threeMoney-xls',
                  filename: '近三个月消费结构excel下载'
                }}
              >
                {
                  this.state.tab === '1'
                    ?
                    <PieChart
                        data={this.state.pieData}
                        padding={[0, 0, 80, 0]}
                        height={this.props.height - 135}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    :
                    <div>
                      <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="small"
                        pagination={this.state.pagination}
                        style={{height:290}}
                      />
                      <Table ref='table' columns={columns} dataSource={this.state.tableData} pagination={false} style={{ display: 'none' }} />
                    </div>
                }
                <div style={{textAlign:"center",color:"rgba(0,0,0,0.75)"}}>近3个月的恩格尔系数为{this.state.engel}%</div>
            </MainCard>
        )
    }
}


