import React, { Component } from 'react';
import { Row, Col, Button, Tooltip } from 'antd';
import { postAction } from "../../../../../axios"
import {hostUrl} from "../../../../../env";


export default class LibrtryCard extends Component {

    state = {
        TotalBook: [],
        OnLineBook: [],
        AvgBook: []
    }

    //累计借阅图书
    getCountTotalBook() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        let year = new Date().getFullYear()
        postAction(
            '/bigdata/book/countTotalBook',
            {
                studentNo: _studentNo,
                year: year
            }
        ).then((res) => {
            if (res.success && res.obj.length > 0 && res.obj[0] !== null) {
                this.setState({
                    TotalBook: [
                        { totalCount: res.obj[0].num, totalName: '累计借阅图书' },
                    ]
                })
            }
        })
    }

    //在借图书数量
    getCountOnLineBook() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        let year = new Date().getFullYear()
        postAction(
            '/bigdata/book/countOnLineBook',
            {
                studentNo: _studentNo,
                year: year
            }
        ).then((res) => {
            if (res.success && res.obj.length > 0 && res.obj[0] !== null) {
                this.setState({
                    OnLineBook: [
                        { totalCount: res.obj[0].num, totalName: '在借图书' }
                    ]
                })
            }
        })
    }

    //月平均借阅
    getCountAvgBook() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        let year = new Date().getFullYear()
        postAction(
            '/bigdata/book/countAvgBook',
            {
                studentNo: _studentNo,
                year: year
            }
        ).then((res) => {
            if (res.success && res.obj.length > 0 && res.obj[0] !== null) {
                this.setState({
                    AvgBook: [
                        { totalCount: res.obj[0].num, totalName: '月均借阅图书' }
                    ]
                })
            }
        })
    }

    //导出明细
    handleMingxi=()=>{
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo;
        let year = new Date().getFullYear();
        window.location.href=`${hostUrl}/bigdata/book/exportBookBorrowDist?borrowCode=${_studentNo}&year=${year}`;
    }

    componentDidMount() {
        this.getCountTotalBook()
        this.getCountOnLineBook()
        this.getCountAvgBook()
    }

    render() {
        const { TotalBook, OnLineBook, AvgBook } = this.state
        return (
            <Row>
                <Col span={8} className="card-bg">
                    <Tooltip placement="top" title='导出明细'>
                        <Button
                            theme="outlined"
                            style={{float:"right", margin:"10px 10px 0 0 ",padding:"0px 8px"}}
                            onClick={this.handleMingxi}
                        >
                            <i className="iconfont icon-daochu" />
                        </Button>
                    </Tooltip>
                    <div className="card-content">
                        {
                            TotalBook && TotalBook.length > 0
                                ?
                                <div>
                                    <p><span>{TotalBook[0].totalCount}</span>本</p>
                                    <p>本年度{TotalBook[0].totalName}</p>
                                </div>
                                :
                                <div style={{ position: 'relative', top: 24, fontSize: 18 }}>暂无数据</div>
                        }
                    </div>
                </Col>
                <Col span={8} className="card-bg">
                    <div className="card-content">
                        {
                            OnLineBook && OnLineBook.length > 0
                                ?
                                <div>
                                    <p><span>{OnLineBook[0].totalCount}</span>本</p>
                                    <p>本年度{OnLineBook[0].totalName}</p>
                                </div>
                                :
                                <div style={{ position: 'relative', top: 24, fontSize: 18 }}>暂无数据</div>
                        }
                    </div>
                </Col>
                <Col span={8} className="card-bg">
                    <div className="card-content">
                        {
                            AvgBook && AvgBook.length > 0
                                ?
                                <div>
                                    <p><span>{AvgBook[0].totalCount}</span>本</p>
                                    <p>本年度{AvgBook[0].totalName}</p>
                                </div>
                                :
                                <div style={{ position: 'relative', top: 24, fontSize: 18 }}>暂无数据</div>
                        }
                    </div>
                </Col>
            </Row>
        )
    }
}


