import React, { Component } from 'react';
import { Card, Popover, Icon } from 'antd';
import PeopleRelation from "../../../../../common/components/D3/PeopleRelation";
import { postAction } from "../../../../../axios"

export default class GerenGuanxi extends Component {

    state = {
        relationInfo: {
            "nodes": [
                // {
                //     "image": "",
                //     "role": "girl",
                //     "name": "吴智蕾",
                //     ""
                // }
            ],
            "edges": [
                // {
                //     "source": 0,
                //     "target": 1,
                //     "relation": "同学"
                // }
            ]
        },
        totalCount: 0,
        classmateCount: 0,
        teacherCount: 0,
    }

    getRelationInfo() {
        let _studentNo = JSON.parse(localStorage.getItem("studentItem")).studentNo
        postAction(
            '/bigdata/portrait/getGroupRelation',
            {
                studentNo: _studentNo
            }
        ).then((res) => {
            if (res.success) {
                if (res.obj !== '' && res.obj !== '') {
                    let relationInfo = res.obj;
                    this.setState({
                        relationInfo: {
                            "nodes": relationInfo.nodes,
                            "edges": relationInfo.edges
                        },
                        // totalCount: relationInfo.classmateCount + relationInfo.teacherCount,
                        // classmateCount: relationInfo.classmateCount,
                        // teacherCount: relationInfo.teacherCount,
                    })
                } else {
                    this.setState({
                        relationInfo: {
                            "nodes": [],
                            "edges": []
                        },
                        totalCount: 0,
                        classmateCount: 0,
                        teacherCount: 0
                    })
                }
            }
        })
    }

    componentDidMount() {
        this.getRelationInfo()
    }

    render() {

        const content = (
            <div>
                <p style={{margin:"0px"}}>根据近一年的消费数据计算</p>
            </div>
        );

        
        return (
            <Card
                title={<div className="common-title">个人群体关系</div>}
                headStyle={{ border: 'none' }}
                className="geren"
                extra={
                    <Popover content={content} title="个人群体关系定义">
                        <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)", cursor: "pointer"}} />
                    </Popover>
                }
            >
                <div className="relation">
                    <PeopleRelation
                        className="relation-box"
                        data={this.state.relationInfo}
                    />
                </div>
                {/*<div className="people-count">*/}
                    {/*迄今为止，该生于本校{this.state.totalCount}人有或远或近的联系：同学关系<span>{this.state.classmateCount}</span>人、师生关系<span>{this.state.teacherCount}</span>人。*/}
                {/*</div>*/}
            </Card>
        )
    }
}


